@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhaina+2:wght@400;500&family=Ubuntu:wght@500;700&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	text-decoration: none;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root {
    --bg-colour: #13293D;
    --fg-colour: #D8DBE2;
    --alt-colour1: #3E5C76;
    --correct-colour: #20BF55;
    --false-colour: #FF6663;
}
@-webkit-keyframes fadeIn{
    from {opacity: 0}
    to {opacity: 1}
}
@keyframes fadeIn{
    from {opacity: 0}
    to {opacity: 1}
}
body {
    background-color: var(--bg-colour);
}

p, a{
    color: var(--fg-colour);
    font-family: 'Baloo Bhaina 2', sans-serif;
    font-size: 1.3rem;
}

h1 {
    color: var(--fg-colour);
    font-family: 'Ubuntu', sans-serif;
    font-size: 2rem;
}

h2 {
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.75rem;
}

h3 {
    color: var(--fg-colour);
    font-family: 'Baloo Bhaina 2', sans-serif;
    font-size: 1.6rem;
}

.buttonNav {
    padding: 20px 30px;
    border-radius: 5px;
    background-color: var(--fg-colour);
    color: var(--bg-colour);
}

.buttonWrapper {
    display: flex;
    justify-content: center;
    padding-top: 150px;
}

.titleText {
    text-align: center;
    padding: 50px 0;
    font-weight: 600;
}

.answerBody {
    box-sizing: border-box;
    border-radius: 30px;
    width: 100%;
    height: 60px;
    border: var(--fg-colour) solid 2px;
    display: flex;
    align-items: center;
    padding: 0 15px;
    margin: 30px 0;
    cursor: pointer;
}

.answerBody.correct {
    background-color: var(--correct-colour);
}

.answerBody.correct .answerPoint{
    background-color: var(--fg-colour);
}

.answerBody.incorrect {
    background-color: var(--false-colour);
}

.answerBody.incorrect .answerPoint{
    background-color: var(--fg-colour);
}

.answerPoint {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: var(--fg-colour) solid 4px;
}

.answerText {
    padding: 8px 15px 0;
}

.questionWrapper {
    display: inline-block;
    margin: auto;
    opacity: 0;
    transition: opacity 1s;
}

.questionWrapperFrame {
    display: flex;
    justify-content: center;
    padding: 0 30px;
}

.questionWrapper.show {
    -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
    opacity: 1;
}

.question {
    text-align: center;
    font-weight: 600;
}

.resultsWrapper {
    text-align: center;
    padding: 100px 0;
    -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
}

